import * as React from 'react';
import { MarkDown ,MarkDownMode } from 'react-markdown-ts';
import readme from './readme'

interface IApp{
    title?:string,
    marddownstring?:string,
}
export default class App extends React.Component<{},IApp>{
    constructor(pros:{}){
        super(pros);
        this.state={
            title:'jssdk',
            marddownstring:"### markdown is loading",
        }
        readme().then(res=>{
            console.log({res})
            this.setState({
                'marddownstring':res.toString()
            });
        })
    }
    get value():string{
        return this.state.marddownstring
    }
    render(){
        
        return (
            <div className='stage'>
                <div>JSSDK LIST</div>
                
               <MarkDown  mode={MarkDownMode.VIEW}  defaultValue={this.value} value={this.state.marddownstring} ></MarkDown>
            </div>
            
        )
    }
}