import {HttpRequest} from '../../src/core/HttpRequest'; 
import { IResponse } from '../../src/I/IResponse';
export default async ()=>{
    const req:HttpRequest=new HttpRequest();
    const res:IResponse=await req.request({
        url:'readme.md',
        method:'get',
        dataType:'txt',
        data:{}
    });
    return res
}